import React, { useState, useEffect } from 'react'
import { useForm, Controller } from 'react-hook-form'
import { useMutation, useQuery, useQueryClient } from '@tanstack/react-query'
import { useParams, useNavigate } from 'react-router-dom'
import { ArrowLeft, Save, TestTube, FileText, Server, Mail, AlertTriangle, CheckCircle } from 'lucide-react'
import { adapterApi } from '@/lib/api'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/card'
import { useNotifications } from '@/stores/ui'

interface AdapterFormData {
  name: string
  description: string
  type: 'FILE' | 'SFTP' | 'EMAIL'
  direction: 'INBOUND' | 'OUTBOUND'
  enabled: boolean
  configuration: Record<string, any>
}

const AdapterConfiguration: React.FC = () => {
  const { id } = useParams()
  const navigate = useNavigate()
  const { success, error } = useNotifications()
  const queryClient = useQueryClient()
  const [_selectedType, setSelectedType] = useState<'FILE' | 'SFTP' | 'EMAIL'>('FILE')
  const [testResult, setTestResult] = useState<any>(null)

  const isEditing = Boolean(id)

  const { data: existingAdapter, isLoading: adapterLoading } = useQuery({
    queryKey: ['interface', id],
    queryFn: () => adapterApi.getInterface(id!),
    enabled: isEditing,
  })

  const { register, handleSubmit, control, watch, setValue, formState: { errors } } = useForm<AdapterFormData>({
    defaultValues: {
      name: '',
      description: '',
      type: 'FILE',
      direction: 'INBOUND',
      enabled: true,
      configuration: {},
    },
  })

  const watchType = watch('type')
  const watchDirection = watch('direction')

  useEffect(() => {
    if (existingAdapter?.data) {
      const adapter = existingAdapter.data
      setValue('name', adapter.name)
      setValue('description', adapter.description)
      setValue('type', adapter.type)
      setValue('direction', adapter.direction)
      setValue('enabled', adapter.enabled)
      setValue('configuration', adapter.configuration || {})
      setSelectedType(adapter.type)
    }
  }, [existingAdapter, setValue])

  const createMutation = useMutation({
    mutationFn: adapterApi.createInterface,
    onSuccess: () => {
      success('Adapter Created', 'Adapter interface created successfully')
      queryClient.invalidateQueries({ queryKey: ['interfaces'] })
      navigate('/adapters')
    },
    onError: (err: any) => {
      error('Creation Failed', err.response?.data?.message || 'Failed to create adapter')
    },
  })

  const updateMutation = useMutation({
    mutationFn: (data: AdapterFormData) => adapterApi.updateInterface(id!, data),
    onSuccess: () => {
      success('Adapter Updated', 'Adapter interface updated successfully')
      queryClient.invalidateQueries({ queryKey: ['interfaces'] })
      queryClient.invalidateQueries({ queryKey: ['interface', id] })
      navigate('/adapters')
    },
    onError: (err: any) => {
      error('Update Failed', err.response?.data?.message || 'Failed to update adapter')
    },
  })

  const testMutation = useMutation({
    mutationFn: (_data: AdapterFormData) => adapterApi.testInterface(id || 'test'),
    onSuccess: (result) => {
      setTestResult(result)
      if (result.data?.success) {
        success('Test Successful', 'Adapter configuration test passed')
      } else {
        error('Test Failed', result.data?.message || 'Adapter test failed')
      }
    },
    onError: (err: any) => {
      error('Test Error', err.response?.data?.message || 'Failed to test adapter configuration')
    },
  })

  const onSubmit = (data: AdapterFormData) => {
    if (isEditing) {
      updateMutation.mutate(data)
    } else {
      createMutation.mutate(data)
    }
  }

  const handleTest = () => {
    const formData = watch()
    testMutation.mutate(formData)
  }

  const getAdapterIcon = (type: string) => {
    switch (type) {
      case 'FILE':
        return <FileText className="h-5 w-5" />
      case 'SFTP':
        return <Server className="h-5 w-5" />
      case 'EMAIL':
        return <Mail className="h-5 w-5" />
      default:
        return <FileText className="h-5 w-5" />
    }
  }

  if (adapterLoading) {
    return (
      <div className="content-spacing">
        <div className="flex items-center justify-center h-64">
          <div className="flex items-center space-x-3">
            <div className="w-8 h-8 border-4 border-primary border-t-transparent rounded-full animate-spin" />
            <span className="text-muted-foreground">Loading adapter configuration...</span>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="content-spacing">
      <div className="flex items-center space-x-4 mb-6">
        <Button variant="outline" size="sm" onClick={() => navigate('/adapters')}>
          <ArrowLeft className="h-4 w-4 mr-2" />
          Back to Adapters
        </Button>
        <div>
          <h1 className="text-3xl font-bold text-foreground mb-2">
            {isEditing ? 'Edit Adapter' : 'Create New Adapter'}
          </h1>
          <p className="text-muted-foreground">
            {isEditing ? 'Modify your adapter configuration' : 'Configure a new file transfer adapter'}
          </p>
        </div>
      </div>

      <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
        {/* Basic Information */}
        <Card className="app-card border">
          <CardHeader>
            <CardTitle className="text-foreground">Basic Information</CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div className="space-y-2">
                <Label htmlFor="name">Adapter Name</Label>
                <Input
                  id="name"
                  placeholder="Enter adapter name"
                  className="bg-input border-border text-foreground"
                  {...register('name', { 
                    required: 'Adapter name is required',
                    minLength: { value: 3, message: 'Name must be at least 3 characters' }
                  })}
                />
                {errors.name && (
                  <p className="text-sm text-destructive">{errors.name.message}</p>
                )}
              </div>

              <div className="space-y-2">
                <Label htmlFor="description">Description</Label>
                <Input
                  id="description"
                  placeholder="Enter adapter description"
                  className="bg-input border-border text-foreground"
                  {...register('description', { 
                    required: 'Description is required' 
                  })}
                />
                {errors.description && (
                  <p className="text-sm text-destructive">{errors.description.message}</p>
                )}
              </div>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div className="space-y-2">
                <Label htmlFor="type">Adapter Type</Label>
                <Controller
                  name="type"
                  control={control}
                  rules={{ required: 'Adapter type is required' }}
                  render={({ field }) => (
                    <select
                      {...field}
                      className="w-full px-3 py-2 bg-input border border-border rounded-md text-foreground"
                      onChange={(e) => {
                        field.onChange(e.target.value)
                        setSelectedType(e.target.value as 'FILE' | 'SFTP' | 'EMAIL')
                      }}
                    >
                      <option value="FILE">File Adapter</option>
                      <option value="SFTP">SFTP Adapter</option>
                      <option value="EMAIL">Email Adapter</option>
                    </select>
                  )}
                />
                {errors.type && (
                  <p className="text-sm text-destructive">{errors.type.message}</p>
                )}
              </div>

              <div className="space-y-2">
                <Label htmlFor="direction">Direction</Label>
                <Controller
                  name="direction"
                  control={control}
                  render={({ field }) => (
                    <select
                      {...field}
                      className="w-full px-3 py-2 bg-input border border-border rounded-md text-foreground"
                    >
                      <option value="INBOUND">Inbound (Receive Files)</option>
                      <option value="OUTBOUND">Outbound (Send Files)</option>
                    </select>
                  )}
                />
              </div>
            </div>

            <div className="flex items-center space-x-2">
              <Controller
                name="enabled"
                control={control}
                render={({ field }) => (
                  <input
                    type="checkbox"
                    id="enabled"
                    checked={field.value}
                    onChange={field.onChange}
                    className="w-4 h-4 text-primary bg-background border-border rounded focus:ring-primary"
                  />
                )}
              />
              <Label htmlFor="enabled">Enable this adapter</Label>
            </div>
          </CardContent>
        </Card>

        {/* Type-Specific Configuration */}
        <Card className="app-card border">
          <CardHeader>
            <CardTitle className="flex items-center space-x-2 text-foreground">
              {getAdapterIcon(watchType)}
              <span>{watchType} Configuration</span>
            </CardTitle>
          </CardHeader>
          <CardContent>
            <TypeSpecificConfiguration
              type={watchType}
              direction={watchDirection}
              control={control}
              register={register}
              errors={errors}
            />
          </CardContent>
        </Card>

        {/* Test Results */}
        {testResult && (
          <Card className="app-card border">
            <CardHeader>
              <CardTitle className="flex items-center space-x-2 text-foreground">
                <TestTube className="h-5 w-5" />
                <span>Test Results</span>
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className={`flex items-start space-x-3 p-4 rounded-md ${
                testResult.data?.success ? 'bg-success/10 border border-success/20' : 'bg-destructive/10 border border-destructive/20'
              }`}>
                {testResult.data?.success ? (
                  <CheckCircle className="h-5 w-5 text-success mt-0.5" />
                ) : (
                  <AlertTriangle className="h-5 w-5 text-destructive mt-0.5" />
                )}
                <div>
                  <p className={`font-medium ${testResult.data?.success ? 'text-success' : 'text-destructive'}`}>
                    {testResult.data?.success ? 'Configuration Test Passed' : 'Configuration Test Failed'}
                  </p>
                  <p className="text-sm text-muted-foreground mt-1">
                    {testResult.data?.message || 'No additional details available'}
                  </p>
                </div>
              </div>
            </CardContent>
          </Card>
        )}

        {/* Action Buttons */}
        <div className="flex items-center space-x-4">
          <Button
            type="submit"
            className="btn-primary"
            disabled={createMutation.isPending || updateMutation.isPending}
          >
            <Save className="h-4 w-4 mr-2" />
            {createMutation.isPending || updateMutation.isPending 
              ? (isEditing ? 'Updating...' : 'Creating...') 
              : (isEditing ? 'Update Adapter' : 'Create Adapter')
            }
          </Button>

          <Button
            type="button"
            variant="outline"
            onClick={handleTest}
            disabled={testMutation.isPending}
          >
            <TestTube className="h-4 w-4 mr-2" />
            {testMutation.isPending ? 'Testing...' : 'Test Configuration'}
          </Button>

          <Button
            type="button"
            variant="outline"
            onClick={() => navigate('/adapters')}
          >
            Cancel
          </Button>
        </div>
      </form>
    </div>
  )
}

// Type-specific configuration components
const TypeSpecificConfiguration: React.FC<{
  type: 'FILE' | 'SFTP' | 'EMAIL'
  direction: 'INBOUND' | 'OUTBOUND'
  control: any
  register: any
  errors: any
}> = ({ type, direction, control, register, errors }) => {
  switch (type) {
    case 'FILE':
      return <FileAdapterConfig direction={direction} control={control} register={register} errors={errors} />
    case 'SFTP':
      return <SftpAdapterConfig direction={direction} control={control} register={register} errors={errors} />
    case 'EMAIL':
      return <EmailAdapterConfig direction={direction} control={control} register={register} errors={errors} />
    default:
      return null
  }
}

// File Adapter Configuration
const FileAdapterConfig: React.FC<any> = ({ direction, register, errors }) => (
  <div className="space-y-4">
    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
      <div className="space-y-2">
        <Label htmlFor="sourceDirectory">Source Directory</Label>
        <Input
          id="sourceDirectory"
          placeholder={direction === 'INBOUND' ? '/data/incoming' : '/data/outgoing'}
          className="bg-input border-border text-foreground"
          {...register('configuration.sourceDirectory', { 
            required: 'Source directory is required' 
          })}
        />
        {errors.configuration?.sourceDirectory && (
          <p className="text-sm text-destructive">{errors.configuration.sourceDirectory.message}</p>
        )}
      </div>

      <div className="space-y-2">
        <Label htmlFor="targetDirectory">Target Directory</Label>
        <Input
          id="targetDirectory"
          placeholder={direction === 'INBOUND' ? '/data/processed' : '/data/sent'}
          className="bg-input border-border text-foreground"
          {...register('configuration.targetDirectory')}
        />
      </div>
    </div>

    <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
      <div className="space-y-2">
        <Label htmlFor="filePattern">File Pattern</Label>
        <Input
          id="filePattern"
          placeholder="*.csv"
          className="bg-input border-border text-foreground"
          {...register('configuration.filePattern')}
        />
      </div>

      <div className="space-y-2">
        <Label htmlFor="archiveDirectory">Archive Directory</Label>
        <Input
          id="archiveDirectory"
          placeholder="/data/archive"
          className="bg-input border-border text-foreground"
          {...register('configuration.archiveDirectory')}
        />
      </div>

      <div className="space-y-2">
        <Label htmlFor="pollingInterval">Polling Interval (ms)</Label>
        <Input
          id="pollingInterval"
          type="number"
          placeholder="5000"
          className="bg-input border-border text-foreground"
          {...register('configuration.pollingInterval', { 
            valueAsNumber: true 
          })}
        />
      </div>
    </div>
  </div>
)

// SFTP Adapter Configuration  
const SftpAdapterConfig: React.FC<any> = ({ direction, register, errors }) => (
  <div className="space-y-4">
    <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
      <div className="space-y-2">
        <Label htmlFor="host">SFTP Host</Label>
        <Input
          id="host"
          placeholder="sftp.example.com"
          className="bg-input border-border text-foreground"
          {...register('configuration.host', { 
            required: 'SFTP host is required' 
          })}
        />
        {errors.configuration?.host && (
          <p className="text-sm text-destructive">{errors.configuration.host.message}</p>
        )}
      </div>

      <div className="space-y-2">
        <Label htmlFor="port">Port</Label>
        <Input
          id="port"
          type="number"
          placeholder="22"
          className="bg-input border-border text-foreground"
          {...register('configuration.port', { 
            valueAsNumber: true,
            required: 'Port is required' 
          })}
        />
        {errors.configuration?.port && (
          <p className="text-sm text-destructive">{errors.configuration.port.message}</p>
        )}
      </div>

      <div className="space-y-2">
        <Label htmlFor="username">Username</Label>
        <Input
          id="username"
          placeholder="username"
          className="bg-input border-border text-foreground"
          {...register('configuration.username', { 
            required: 'Username is required' 
          })}
        />
        {errors.configuration?.username && (
          <p className="text-sm text-destructive">{errors.configuration.username.message}</p>
        )}
      </div>
    </div>

    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
      <div className="space-y-2">
        <Label htmlFor="localDirectory">Local Directory</Label>
        <Input
          id="localDirectory"
          placeholder={direction === 'INBOUND' ? '/data/downloaded' : '/data/uploads'}
          className="bg-input border-border text-foreground"
          {...register('configuration.localDirectory', { 
            required: 'Local directory is required' 
          })}
        />
      </div>

      <div className="space-y-2">
        <Label htmlFor="remoteDirectory">Remote Directory</Label>
        <Input
          id="remoteDirectory"
          placeholder="/remote/path"
          className="bg-input border-border text-foreground"
          {...register('configuration.remoteDirectory', { 
            required: 'Remote directory is required' 
          })}
        />
      </div>
    </div>
  </div>
)

// Email Adapter Configuration
const EmailAdapterConfig: React.FC<any> = ({ direction, register, errors }) => (
  <div className="space-y-4">
    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
      <div className="space-y-2">
        <Label htmlFor="smtpHost">SMTP Host</Label>
        <Input
          id="smtpHost"
          placeholder="smtp.gmail.com"
          className="bg-input border-border text-foreground"
          {...register('configuration.smtpHost', { 
            required: 'SMTP host is required' 
          })}
        />
        {errors.configuration?.smtpHost && (
          <p className="text-sm text-destructive">{errors.configuration.smtpHost.message}</p>
        )}
      </div>

      <div className="space-y-2">
        <Label htmlFor="smtpPort">SMTP Port</Label>
        <Input
          id="smtpPort"
          type="number"
          placeholder="587"
          className="bg-input border-border text-foreground"
          {...register('configuration.smtpPort', { 
            valueAsNumber: true,
            required: 'SMTP port is required' 
          })}
        />
        {errors.configuration?.smtpPort && (
          <p className="text-sm text-destructive">{errors.configuration.smtpPort.message}</p>
        )}
      </div>
    </div>

    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
      <div className="space-y-2">
        <Label htmlFor="smtpUsername">Email Username</Label>
        <Input
          id="smtpUsername"
          placeholder="notifications@company.com"
          className="bg-input border-border text-foreground"
          {...register('configuration.smtpUsername', { 
            required: 'Email username is required' 
          })}
        />
      </div>

      <div className="space-y-2">
        <Label htmlFor="smtpPassword">Email Password</Label>
        <Input
          id="smtpPassword"
          type="password"
          placeholder="Enter email password"
          className="bg-input border-border text-foreground"
          {...register('configuration.smtpPassword', { 
            required: 'Email password is required' 
          })}
        />
      </div>
    </div>

    {direction === 'OUTBOUND' && (
      <div className="space-y-4">
        <div className="space-y-2">
          <Label htmlFor="recipients">Recipients (comma-separated)</Label>
          <Input
            id="recipients"
            placeholder="user1@example.com, user2@example.com"
            className="bg-input border-border text-foreground"
            {...register('configuration.recipients', { 
              required: 'Recipients are required for outbound email' 
            })}
          />
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <div className="space-y-2">
            <Label htmlFor="subject">Email Subject</Label>
            <Input
              id="subject"
              placeholder="File Transfer Notification"
              className="bg-input border-border text-foreground"
              {...register('configuration.subject')}
            />
          </div>

          <div className="space-y-2">
            <Label htmlFor="attachmentDirectory">Attachment Directory</Label>
            <Input
              id="attachmentDirectory"
              placeholder="/data/attachments"
              className="bg-input border-border text-foreground"
              {...register('configuration.attachmentDirectory')}
            />
          </div>
        </div>
      </div>
    )}
  </div>
)

export default AdapterConfiguration