import React from 'react'
import { Link, useLocation } from 'react-router-dom'
import { 
  LayoutDashboard,
  Settings,
  FileText,
  Users,
  Key,
  Activity,
  Building2,
  ChevronLeft,
  ChevronRight,
  Boxes
} from 'lucide-react'
import { cn } from '@/lib/utils'
import { useUIStore } from '@/stores/ui'
import { useAuthStore } from '@/stores/auth'
import { Button } from '@/components/ui/button'

const navigation = [
  { name: 'Dashboard', href: '/dashboard', icon: LayoutDashboard },
  { name: 'Adapters', href: '/adapters', icon: Boxes },
  { name: 'File Management', href: '/files', icon: FileText },
  { name: 'SSH Keys', href: '/ssh-keys', icon: Key },
  { name: 'Users', href: '/users', icon: Users, adminOnly: true },
  { name: 'Monitoring', href: '/monitoring', icon: Activity },
  { name: 'Logs', href: '/logs', icon: Settings },
]

const Sidebar: React.FC = () => {
  const location = useLocation()
  const { sidebarOpen, toggleSidebar } = useUIStore()
  const { user } = useAuthStore()

  const isAdmin = user?.role === 'ADMINISTRATOR'

  const filteredNavigation = navigation.filter(item => 
    !item.adminOnly || isAdmin
  )

  return (
    <div className={cn(
      "fixed inset-y-0 left-0 z-50 bg-sidebar border-r border-sidebar-border transition-all duration-300",
      sidebarOpen ? "w-64" : "w-16"
    )}>
      <div className="flex flex-col h-full">
        {/* Logo */}
        <div className="flex items-center px-6 py-4 border-b border-sidebar-border">
          <Building2 className="h-8 w-8 text-sidebar-primary flex-shrink-0" />
          {sidebarOpen && (
            <h1 className="ml-3 text-xl font-semibold text-sidebar-foreground app-name-gradient">
              H2H Transfer
            </h1>
          )}
        </div>

        {/* Navigation */}
        <nav className="flex-1 px-4 py-6 space-y-2">
          {filteredNavigation.map((item) => {
            const isActive = location.pathname.startsWith(item.href)
            
            return (
              <Link
                key={item.name}
                to={item.href}
                className={cn(
                  'flex items-center px-3 py-2 rounded-md text-sm font-medium transition-colors',
                  isActive
                    ? 'bg-sidebar-accent text-sidebar-accent-foreground'
                    : 'text-sidebar-foreground hover:bg-sidebar-accent hover:text-sidebar-accent-foreground'
                )}
                title={!sidebarOpen ? item.name : undefined}
              >
                <item.icon className="h-5 w-5 mr-3" />
                {sidebarOpen && (
                  <span>{item.name}</span>
                )}
              </Link>
            )
          })}
        </nav>

        {/* Toggle button */}
        <div className="border-t border-sidebar-border p-4">
          <Button
            variant="ghost"
            size="sm"
            onClick={toggleSidebar}
            className={cn(
              "w-full justify-center text-sidebar-foreground hover:bg-sidebar-accent",
              !sidebarOpen && "px-0"
            )}
          >
            {sidebarOpen ? (
              <>
                <ChevronLeft className="h-4 w-4 mr-2" />
                Collapse
              </>
            ) : (
              <ChevronRight className="h-4 w-4" />
            )}
          </Button>
        </div>
      </div>
    </div>
  )
}

export default Sidebar