// File Management API endpoints
export const fileApi = {
  getAllAdapters: async () => {
    const response = await api.get('/api/interfaces')
    return response.data
  },
  
  getAdapterFiles: async (adapterName: string, directory: 'outbound' | 'inbound' | 'archive', filters?: { pattern?: string; dateFrom?: string }) => {
    let url = `/api/files/${adapterName}/${directory}`
    const params = new URLSearchParams()
    
    if (filters?.pattern) params.append('pattern', filters.pattern)
    if (filters?.dateFrom) params.append('dateFrom', filters.dateFrom)
    
    if (params.toString()) {
      url += `?${params.toString()}`
    }
    
    const response = await api.get(url)
    return response.data
  },
  
  getFileInfo: async (filePath: string) => {
    const response = await api.get(`/api/files/info?filePath=${encodeURIComponent(filePath)}`)
    return response.data
  },
  
  downloadSingleFile: async ({ filePath }: { filePath: string; fileName: string }) => {
    const response = await api.get(`/api/files/download?filePath=${encodeURIComponent(filePath)}`, {
      responseType: 'blob',
    })
    return response.data
  },
  
  downloadMultipleFiles: async ({ filePaths, archiveName }: { filePaths: string[]; archiveName: string }) => {
    const response = await api.post('/api/files/download-zip', {
      filePaths,
      archiveName,
    }, {
      responseType: 'blob',
    })
    return response.data
  },
  
  searchFiles: async (adapterName: string, pattern: string, directory: string) => {
    const response = await api.get(
      `/api/files/${adapterName}/search?pattern=${encodeURIComponent(pattern)}&directory=${directory}`
    )
    return response.data
  },
  
  getFileStats: async () => {
    const response = await api.get('/api/files/stats')
    return response.data
  },
}

