import React, { useState } from 'react'
import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query'
import { Plus, Key, Download, Trash2, Edit, Eye, EyeOff, CheckCircle, XCircle, Copy } from 'lucide-react'
import { sshKeyApi } from '@/lib/api'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/card'
import { useNotifications } from '@/stores/ui'
import { usePermissions } from '@/hooks/auth'

interface SshKey {
  id: string
  name: string
  description: string
  keyType: 'RSA' | 'DSA'
  keySize: number
  fingerprint: string
  publicKey: string
  enabled: boolean
  createdAt: string
  lastUsed?: string
  usageCount: number
}

interface CreateKeyRequest {
  name: string
  description: string
  keyType: 'RSA' | 'DSA'
  keySize: number
}

const SshKeyManagement: React.FC = () => {
  const { isAdmin } = usePermissions()
  const { success, error } = useNotifications()
  const queryClient = useQueryClient()
  
  const [showCreateForm, setShowCreateForm] = useState(false)
  const [showPrivateKeys, setShowPrivateKeys] = useState<Record<string, boolean>>({})
  const [_editingKey, setEditingKey] = useState<string | null>(null)

  // Fetch SSH keys
  const { data: sshKeys, isLoading } = useQuery<SshKey[]>({
    queryKey: ['ssh-keys'],
    queryFn: sshKeyApi.getAllKeys,
  })

  // Mutations
  const createKeyMutation = useMutation({
    mutationFn: (keyData: CreateKeyRequest) => 
      keyData.keyType === 'RSA' 
        ? sshKeyApi.generateRSAKey(keyData)
        : sshKeyApi.generateDSAKey(keyData),
    onSuccess: () => {
      success('SSH Key Generated', 'SSH key pair created successfully')
      queryClient.invalidateQueries({ queryKey: ['ssh-keys'] })
      setShowCreateForm(false)
    },
    onError: (err: any) => {
      error('Generation Failed', err.response?.data?.message || 'Failed to generate SSH key')
    },
  })


  const toggleKeyStatusMutation = useMutation({
    mutationFn: ({ id, enabled }: { id: string; enabled: boolean }) => 
      sshKeyApi.toggleKeyStatus(id, enabled),
    onSuccess: () => {
      success('Status Updated', 'SSH key status changed successfully')
      queryClient.invalidateQueries({ queryKey: ['ssh-keys'] })
    },
    onError: (err: any) => {
      error('Status Change Failed', err.response?.data?.message || 'Failed to change key status')
    },
  })

  const deleteKeyMutation = useMutation({
    mutationFn: sshKeyApi.deleteKey,
    onSuccess: () => {
      success('Key Deleted', 'SSH key deleted successfully')
      queryClient.invalidateQueries({ queryKey: ['ssh-keys'] })
    },
    onError: (err: any) => {
      error('Deletion Failed', err.response?.data?.message || 'Failed to delete SSH key')
    },
  })

  const handleDownloadPrivateKey = async (keyId: string, keyName: string) => {
    try {
      const blob = await sshKeyApi.downloadPrivateKey(keyId)
      const url = window.URL.createObjectURL(blob)
      const link = document.createElement('a')
      link.href = url
      link.download = `${keyName}_private_key`
      document.body.appendChild(link)
      link.click()
      document.body.removeChild(link)
      window.URL.revokeObjectURL(url)
      success('Download Complete', 'Private key downloaded successfully')
    } catch (err: any) {
      error('Download Failed', err.response?.data?.message || 'Failed to download private key')
    }
  }

  const handleDownloadPublicKey = async (keyId: string, keyName: string) => {
    try {
      const blob = await sshKeyApi.downloadPublicKey(keyId)
      const url = window.URL.createObjectURL(blob)
      const link = document.createElement('a')
      link.href = url
      link.download = `${keyName}_public_key.pub`
      document.body.appendChild(link)
      link.click()
      document.body.removeChild(link)
      window.URL.revokeObjectURL(url)
      success('Download Complete', 'Public key downloaded successfully')
    } catch (err: any) {
      error('Download Failed', err.response?.data?.message || 'Failed to download public key')
    }
  }

  const handleCopyPublicKey = (publicKey: string) => {
    navigator.clipboard.writeText(publicKey).then(() => {
      success('Copied', 'Public key copied to clipboard')
    }).catch(() => {
      error('Copy Failed', 'Failed to copy public key to clipboard')
    })
  }

  const togglePrivateKeyVisibility = (keyId: string) => {
    setShowPrivateKeys(prev => ({
      ...prev,
      [keyId]: !prev[keyId]
    }))
  }

  if (isLoading) {
    return (
      <div className="content-spacing">
        <div className="flex items-center justify-center h-64">
          <div className="flex items-center space-x-3">
            <div className="w-8 h-8 border-4 border-primary border-t-transparent rounded-full animate-spin" />
            <span className="text-muted-foreground">Loading SSH keys...</span>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="content-spacing">
      <div className="flex items-center justify-between mb-6">
        <div>
          <h1 className="text-3xl font-bold text-foreground mb-2">SSH Key Management</h1>
          <p className="text-muted-foreground">Generate and manage SSH keys for secure SFTP connections</p>
        </div>
        {isAdmin() && (
          <Button 
            className="btn-primary"
            onClick={() => setShowCreateForm(true)}
          >
            <Plus className="h-4 w-4 mr-2" />
            Generate New Key
          </Button>
        )}
      </div>

      {/* Create Key Form */}
      {showCreateForm && (
        <Card className="app-card border mb-6">
          <CardHeader>
            <CardTitle className="text-foreground flex items-center space-x-2">
              <Key className="h-5 w-5" />
              <span>Generate SSH Key Pair</span>
            </CardTitle>
          </CardHeader>
          <CardContent>
            <CreateKeyForm
              onSubmit={(data) => createKeyMutation.mutate(data)}
              onCancel={() => setShowCreateForm(false)}
              isLoading={createKeyMutation.isPending}
            />
          </CardContent>
        </Card>
      )}

      {/* SSH Keys List */}
      {sshKeys && sshKeys.length > 0 ? (
        <div className="grid grid-cols-1 gap-6">
          {sshKeys.map((key) => (
            <Card key={key.id} className="app-card border hover-scale animate-fade-in">
              <CardHeader>
                <div className="flex items-center justify-between">
                  <div className="flex items-center space-x-3">
                    <Key className="h-6 w-6 text-primary" />
                    <div>
                      <CardTitle className="text-lg text-foreground">{key.name}</CardTitle>
                      <p className="text-sm text-muted-foreground">{key.description}</p>
                    </div>
                  </div>
                  <div className="flex items-center space-x-2">
                    <div className={`w-3 h-3 rounded-full ${
                      key.enabled ? 'bg-success animate-glow' : 'bg-muted-foreground'
                    }`} />
                    <span className={`text-xs px-2 py-1 rounded ${
                      key.enabled ? 'bg-success/20 text-success' : 'bg-muted/20 text-muted-foreground'
                    }`}>
                      {key.enabled ? 'Enabled' : 'Disabled'}
                    </span>
                  </div>
                </div>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  {/* Key Details */}
                  <div className="grid grid-cols-2 md:grid-cols-4 gap-4 text-sm">
                    <div>
                      <span className="text-muted-foreground">Type:</span>
                      <span className="ml-2 text-foreground">{key.keyType} {key.keySize}</span>
                    </div>
                    <div>
                      <span className="text-muted-foreground">Created:</span>
                      <span className="ml-2 text-foreground">{new Date(key.createdAt).toLocaleDateString()}</span>
                    </div>
                    <div>
                      <span className="text-muted-foreground">Usage:</span>
                      <span className="ml-2 text-foreground">{key.usageCount} times</span>
                    </div>
                    <div>
                      <span className="text-muted-foreground">Last Used:</span>
                      <span className="ml-2 text-foreground">
                        {key.lastUsed ? new Date(key.lastUsed).toLocaleDateString() : 'Never'}
                      </span>
                    </div>
                  </div>

                  {/* Fingerprint */}
                  <div>
                    <label className="text-sm font-medium text-foreground">Fingerprint:</label>
                    <div className="mt-1 font-mono text-xs bg-secondary p-2 rounded">
                      {key.fingerprint}
                    </div>
                  </div>

                  {/* Public Key */}
                  <div>
                    <div className="flex items-center justify-between">
                      <label className="text-sm font-medium text-foreground">Public Key:</label>
                      <div className="flex items-center space-x-2">
                        <Button 
                          variant="outline" 
                          size="sm"
                          onClick={() => handleCopyPublicKey(key.publicKey)}
                        >
                          <Copy className="h-4 w-4" />
                        </Button>
                        <Button
                          variant="outline"
                          size="sm"
                          onClick={() => togglePrivateKeyVisibility(key.id)}
                        >
                          {showPrivateKeys[key.id] ? <EyeOff className="h-4 w-4" /> : <Eye className="h-4 w-4" />}
                        </Button>
                      </div>
                    </div>
                    <div className="mt-1 font-mono text-xs bg-secondary p-2 rounded break-all">
                      {showPrivateKeys[key.id] ? key.publicKey : `${key.publicKey.substring(0, 50)}...`}
                    </div>
                  </div>

                  {/* Action Buttons */}
                  <div className="flex items-center space-x-2 pt-4 border-t border-border">
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() => handleDownloadPublicKey(key.id, key.name)}
                    >
                      <Download className="h-4 w-4 mr-2" />
                      Download Public
                    </Button>
                    
                    {isAdmin() && (
                      <>
                        <Button
                          variant="outline"
                          size="sm"
                          onClick={() => handleDownloadPrivateKey(key.id, key.name)}
                        >
                          <Download className="h-4 w-4 mr-2" />
                          Download Private
                        </Button>
                        
                        <Button
                          variant="outline"
                          size="sm"
                          onClick={() => 
                            toggleKeyStatusMutation.mutate({ id: key.id, enabled: !key.enabled })
                          }
                          disabled={toggleKeyStatusMutation.isPending}
                        >
                          {key.enabled ? (
                            <>
                              <XCircle className="h-4 w-4 mr-2" />
                              Disable
                            </>
                          ) : (
                            <>
                              <CheckCircle className="h-4 w-4 mr-2" />
                              Enable
                            </>
                          )}
                        </Button>
                        
                        <Button
                          variant="outline"
                          size="sm"
                          onClick={() => setEditingKey(key.id)}
                        >
                          <Edit className="h-4 w-4 mr-2" />
                          Edit
                        </Button>
                        
                        <Button
                          variant="outline"
                          size="sm"
                          className="text-destructive hover:bg-destructive/10"
                          onClick={() => deleteKeyMutation.mutate(key.id)}
                          disabled={deleteKeyMutation.isPending}
                        >
                          <Trash2 className="h-4 w-4 mr-2" />
                          Delete
                        </Button>
                      </>
                    )}
                  </div>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
      ) : (
        <Card className="app-card border">
          <CardContent className="flex items-center justify-center py-12">
            <div className="text-center">
              <Key className="h-16 w-16 text-muted-foreground mx-auto mb-4" />
              <h3 className="text-lg font-semibold text-foreground mb-2">No SSH Keys</h3>
              <p className="text-muted-foreground mb-6">Generate your first SSH key pair for secure SFTP connections</p>
              {isAdmin() && (
                <Button 
                  className="btn-primary"
                  onClick={() => setShowCreateForm(true)}
                >
                  <Plus className="h-4 w-4 mr-2" />
                  Generate SSH Key
                </Button>
              )}
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  )
}

const CreateKeyForm: React.FC<{
  onSubmit: (data: CreateKeyRequest) => void
  onCancel: () => void
  isLoading: boolean
}> = ({ onSubmit, onCancel, isLoading }) => {
  const [formData, setFormData] = useState<CreateKeyRequest>({
    name: '',
    description: '',
    keyType: 'RSA',
    keySize: 2048,
  })

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    onSubmit(formData)
  }

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <div className="space-y-2">
          <Label htmlFor="name">Key Name</Label>
          <Input
            id="name"
            value={formData.name}
            onChange={(e) => setFormData(prev => ({ ...prev, name: e.target.value }))}
            placeholder="e.g., production-sftp-key"
            className="bg-input border-border text-foreground"
            required
          />
        </div>

        <div className="space-y-2">
          <Label htmlFor="description">Description</Label>
          <Input
            id="description"
            value={formData.description}
            onChange={(e) => setFormData(prev => ({ ...prev, description: e.target.value }))}
            placeholder="e.g., Key for production SFTP server"
            className="bg-input border-border text-foreground"
            required
          />
        </div>

        <div className="space-y-2">
          <Label htmlFor="keyType">Key Type</Label>
          <select
            id="keyType"
            value={formData.keyType}
            onChange={(e) => setFormData(prev => ({ 
              ...prev, 
              keyType: e.target.value as 'RSA' | 'DSA',
              keySize: e.target.value === 'RSA' ? 2048 : 1024
            }))}
            className="w-full px-3 py-2 bg-input border border-border rounded-md text-foreground"
          >
            <option value="RSA">RSA (Recommended)</option>
            <option value="DSA">DSA</option>
          </select>
        </div>

        <div className="space-y-2">
          <Label htmlFor="keySize">Key Size</Label>
          <select
            id="keySize"
            value={formData.keySize}
            onChange={(e) => setFormData(prev => ({ ...prev, keySize: parseInt(e.target.value) }))}
            className="w-full px-3 py-2 bg-input border border-border rounded-md text-foreground"
          >
            {formData.keyType === 'RSA' ? (
              <>
                <option value={2048}>2048 bits (Recommended)</option>
                <option value={3072}>3072 bits</option>
                <option value={4096}>4096 bits (High Security)</option>
              </>
            ) : (
              <>
                <option value={1024}>1024 bits (Standard)</option>
                <option value={2048}>2048 bits</option>
              </>
            )}
          </select>
        </div>
      </div>

      <div className="flex items-center space-x-4 pt-4">
        <Button type="submit" className="btn-primary" disabled={isLoading}>
          {isLoading ? 'Generating...' : 'Generate Key Pair'}
        </Button>
        <Button type="button" variant="outline" onClick={onCancel}>
          Cancel
        </Button>
      </div>
    </form>
  )
}

export default SshKeyManagement