import React, { useState } from 'react'
import { useQuery, useMutation } from '@tanstack/react-query'
import { Download, FileText, FolderOpen, Search, Calendar, Filter, Archive, Trash2, Eye } from 'lucide-react'
import { fileApi } from '@/lib/api'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/card'
import { useNotifications } from '@/stores/ui'
import { usePermissions } from '@/hooks/auth'

interface FileInfo {
  name: string
  path: string
  size: number
  lastModified: string
  isDirectory: boolean
  checksum?: string
  contentType: string
}

interface FileListResponse {
  files: FileInfo[]
  totalCount: number
  adapterName: string
  directoryPath: string
}

const FileManagement: React.FC = () => {
  const { isAdmin } = usePermissions()
  const { success, error } = useNotifications()
  
  const [selectedAdapter, setSelectedAdapter] = useState<string>('')
  const [selectedDirectory, setSelectedDirectory] = useState<'outbound' | 'inbound' | 'archive'>('outbound')
  const [searchPattern, setSearchPattern] = useState<string>('')
  const [selectedFiles, setSelectedFiles] = useState<string[]>([])
  const [dateFilter, setDateFilter] = useState<string>('')

  // Fetch available adapters
  const { data: adapters } = useQuery({
    queryKey: ['interfaces'],
    queryFn: fileApi.getAllAdapters,
  })

  // Fetch files for selected adapter and directory
  const { data: fileList, isLoading, refetch } = useQuery<FileListResponse>({
    queryKey: ['files', selectedAdapter, selectedDirectory, searchPattern, dateFilter],
    queryFn: () => {
      if (!selectedAdapter) return Promise.resolve({ files: [], totalCount: 0, adapterName: '', directoryPath: '' })
      return fileApi.getAdapterFiles(selectedAdapter, selectedDirectory, {
        pattern: searchPattern || undefined,
        dateFrom: dateFilter || undefined,
      })
    },
    enabled: Boolean(selectedAdapter),
  })

  // Download mutations
  const downloadSingleMutation = useMutation({
    mutationFn: (filePath: string) => fileApi.downloadSingleFile({ filePath, fileName: '' }),
    onSuccess: (blob: Blob, fileName: string) => {
      const url = window.URL.createObjectURL(blob)
      const link = document.createElement('a')
      link.href = url
      link.download = fileName
      document.body.appendChild(link)
      link.click()
      document.body.removeChild(link)
      window.URL.revokeObjectURL(url)
      success('Download Complete', 'File downloaded successfully')
    },
    onError: (err: any) => {
      error('Download Failed', err.response?.data?.message || 'Failed to download file')
    },
  })

  const downloadMultipleMutation = useMutation({
    mutationFn: fileApi.downloadMultipleFiles,
    onSuccess: (blob) => {
      const url = window.URL.createObjectURL(blob)
      const link = document.createElement('a')
      link.href = url
      link.download = `files_${new Date().toISOString().split('T')[0]}.zip`
      document.body.appendChild(link)
      link.click()
      document.body.removeChild(link)
      window.URL.revokeObjectURL(url)
      success('Download Complete', 'Files downloaded as ZIP archive')
      setSelectedFiles([])
    },
    onError: (err: any) => {
      error('Download Failed', err.response?.data?.message || 'Failed to download files')
    },
  })

  const handleSingleDownload = (filePath: string, _fileName: string) => {
    downloadSingleMutation.mutate(filePath)
  }

  const handleMultipleDownload = () => {
    if (selectedFiles.length === 0) {
      error('No Selection', 'Please select files to download')
      return
    }
    
    downloadMultipleMutation.mutate({
      filePaths: selectedFiles,
      archiveName: `${selectedAdapter}_${selectedDirectory}_files.zip`
    })
  }

  const formatFileSize = (bytes: number): string => {
    if (bytes === 0) return '0 B'
    const k = 1024
    const sizes = ['B', 'KB', 'MB', 'GB']
    const i = Math.floor(Math.log(bytes) / Math.log(k))
    return `${parseFloat((bytes / Math.pow(k, i)).toFixed(2))} ${sizes[i]}`
  }

  const formatDate = (dateString: string): string => {
    return new Date(dateString).toLocaleString()
  }

  const toggleFileSelection = (filePath: string) => {
    setSelectedFiles(prev => 
      prev.includes(filePath)
        ? prev.filter(path => path !== filePath)
        : [...prev, filePath]
    )
  }

  const selectAllFiles = () => {
    if (!fileList?.files) return
    
    const allFilePaths = fileList.files
      .filter(file => !file.isDirectory)
      .map(file => file.path)
    
    setSelectedFiles(selectedFiles.length === allFilePaths.length ? [] : allFilePaths)
  }

  if (isLoading) {
    return (
      <div className="content-spacing">
        <div className="flex items-center justify-center h-64">
          <div className="flex items-center space-x-3">
            <div className="w-8 h-8 border-4 border-primary border-t-transparent rounded-full animate-spin" />
            <span className="text-muted-foreground">Loading files...</span>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="content-spacing">
      <div className="flex items-center justify-between mb-6">
        <div>
          <h1 className="text-3xl font-bold text-foreground mb-2">File Management</h1>
          <p className="text-muted-foreground">Download and manage processed files from your adapters</p>
        </div>
        {selectedFiles.length > 0 && (
          <Button 
            className="btn-primary"
            onClick={handleMultipleDownload}
            disabled={downloadMultipleMutation.isPending}
          >
            <Download className="h-4 w-4 mr-2" />
            {downloadMultipleMutation.isPending ? 'Creating ZIP...' : `Download ${selectedFiles.length} Files`}
          </Button>
        )}
      </div>

      {/* Filters */}
      <Card className="app-card border mb-6">
        <CardHeader>
          <CardTitle className="text-foreground flex items-center space-x-2">
            <Filter className="h-5 w-5" />
            <span>File Filters</span>
          </CardTitle>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
            <div className="space-y-2">
              <label className="text-sm font-medium text-foreground">Adapter</label>
              <select
                value={selectedAdapter}
                onChange={(e) => {
                  setSelectedAdapter(e.target.value)
                  setSelectedFiles([])
                }}
                className="w-full px-3 py-2 bg-input border border-border rounded-md text-foreground"
              >
                <option value="">Select Adapter</option>
                {adapters?.map((adapter: any) => (
                  <option key={adapter.id} value={adapter.name}>
                    {adapter.name} ({adapter.type})
                  </option>
                ))}
              </select>
            </div>

            <div className="space-y-2">
              <label className="text-sm font-medium text-foreground">Directory</label>
              <select
                value={selectedDirectory}
                onChange={(e) => {
                  setSelectedDirectory(e.target.value as 'outbound' | 'inbound' | 'archive')
                  setSelectedFiles([])
                }}
                className="w-full px-3 py-2 bg-input border border-border rounded-md text-foreground"
                disabled={!selectedAdapter}
              >
                <option value="outbound">Outbound Files</option>
                <option value="inbound">Inbound Files</option>
                <option value="archive">Archive</option>
              </select>
            </div>

            <div className="space-y-2">
              <label className="text-sm font-medium text-foreground">Search Pattern</label>
              <div className="relative">
                <Search className="absolute left-3 top-3 h-4 w-4 text-muted-foreground" />
                <Input
                  placeholder="*.xml, payment_*"
                  value={searchPattern}
                  onChange={(e) => setSearchPattern(e.target.value)}
                  className="pl-9 bg-input border-border text-foreground"
                  disabled={!selectedAdapter}
                />
              </div>
            </div>

            <div className="space-y-2">
              <label className="text-sm font-medium text-foreground">Modified Date</label>
              <div className="relative">
                <Calendar className="absolute left-3 top-3 h-4 w-4 text-muted-foreground" />
                <Input
                  type="date"
                  value={dateFilter}
                  onChange={(e) => setDateFilter(e.target.value)}
                  className="pl-9 bg-input border-border text-foreground"
                  disabled={!selectedAdapter}
                />
              </div>
            </div>
          </div>

          {selectedAdapter && fileList && (
            <div className="mt-4 text-sm text-muted-foreground">
              Found {fileList.totalCount} files in {fileList.directoryPath}
            </div>
          )}
        </CardContent>
      </Card>

      {/* File List */}
      {selectedAdapter && fileList ? (
        <Card className="app-card border">
          <CardHeader>
            <div className="flex items-center justify-between">
              <CardTitle className="text-foreground flex items-center space-x-2">
                <FolderOpen className="h-5 w-5" />
                <span>{selectedAdapter} - {selectedDirectory} Files</span>
              </CardTitle>
              {fileList.files.length > 0 && (
                <div className="flex items-center space-x-2">
                  <Button 
                    variant="outline" 
                    size="sm" 
                    onClick={selectAllFiles}
                  >
                    {selectedFiles.length === fileList.files.filter(f => !f.isDirectory).length ? 'Deselect All' : 'Select All'}
                  </Button>
                  <Button 
                    variant="outline" 
                    size="sm" 
                    onClick={() => refetch()}
                  >
                    Refresh
                  </Button>
                </div>
              )}
            </div>
          </CardHeader>
          <CardContent>
            {fileList.files.length > 0 ? (
              <div className="space-y-2">
                {fileList.files.map((file) => (
                  <div
                    key={file.path}
                    className={`flex items-center justify-between p-3 rounded-lg border transition-colors ${
                      selectedFiles.includes(file.path) 
                        ? 'bg-primary/10 border-primary/30' 
                        : 'bg-secondary hover:bg-secondary/80'
                    }`}
                  >
                    <div className="flex items-center space-x-3">
                      {!file.isDirectory && (
                        <input
                          type="checkbox"
                          checked={selectedFiles.includes(file.path)}
                          onChange={() => toggleFileSelection(file.path)}
                          className="w-4 h-4 text-primary bg-background border-border rounded focus:ring-primary"
                        />
                      )}
                      <div className="flex items-center space-x-2">
                        {file.isDirectory ? (
                          <FolderOpen className="h-5 w-5 text-info" />
                        ) : (
                          <FileText className="h-5 w-5 text-foreground" />
                        )}
                        <div>
                          <p className="font-medium text-foreground">{file.name}</p>
                          <p className="text-sm text-muted-foreground">
                            {file.isDirectory ? 'Directory' : formatFileSize(file.size)}  {formatDate(file.lastModified)}
                          </p>
                        </div>
                      </div>
                    </div>

                    <div className="flex items-center space-x-2">
                      {!file.isDirectory && (
                        <>
                          <Button
                            variant="outline"
                            size="sm"
                            onClick={() => handleSingleDownload(file.path, file.name)}
                            disabled={downloadSingleMutation.isPending}
                          >
                            <Download className="h-4 w-4" />
                          </Button>
                          <Button
                            variant="outline"
                            size="sm"
                          >
                            <Eye className="h-4 w-4" />
                          </Button>
                          {isAdmin() && (
                            <Button
                              variant="outline"
                              size="sm"
                              className="text-destructive hover:bg-destructive/10"
                            >
                              <Trash2 className="h-4 w-4" />
                            </Button>
                          )}
                        </>
                      )}
                    </div>
                  </div>
                ))}
              </div>
            ) : (
              <div className="flex items-center justify-center py-12">
                <div className="text-center">
                  <FileText className="h-16 w-16 text-muted-foreground mx-auto mb-4" />
                  <h3 className="text-lg font-semibold text-foreground mb-2">No Files Found</h3>
                  <p className="text-muted-foreground">
                    {selectedAdapter 
                      ? `No files found in ${selectedDirectory} directory for ${selectedAdapter}` 
                      : 'Select an adapter to view files'
                    }
                  </p>
                </div>
              </div>
            )}
          </CardContent>
        </Card>
      ) : (
        <Card className="app-card border">
          <CardContent className="flex items-center justify-center py-12">
            <div className="text-center">
              <Archive className="h-16 w-16 text-muted-foreground mx-auto mb-4" />
              <h3 className="text-lg font-semibold text-foreground mb-2">Select an Adapter</h3>
              <p className="text-muted-foreground">Choose an adapter from the filter above to view and download files</p>
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  )
}

export default FileManagement