import { Routes, Route, Navigate } from 'react-router-dom'
import { ReactQueryDevtools } from '@tanstack/react-query-devtools'
import { Toaster } from 'react-hot-toast'

// Components
import Layout from './components/Layout'
import ProtectedRoute from './components/ProtectedRoute'

// Pages
import LoginPage from './pages/auth/LoginPage'
import { Dashboard } from './pages/Dashboard'
import AdapterConfiguration from './pages/adapters/AdapterConfiguration'
import AdapterList from './pages/adapters/AdapterList'
import FileManagement from './pages/files/FileManagement'
import SshKeyManagement from './pages/ssh-keys/SshKeyManagement'
import UserManagement from './pages/users/UserManagement'
import Monitoring from './pages/Monitoring'
import Logs from './pages/Logs'

function App() {
  return (
    <div className="min-h-screen bg-background">
      <Routes>
            {/* Public routes */}
            <Route path="/login" element={<LoginPage />} />
            
            {/* Protected routes */}
            <Route path="/" element={<ProtectedRoute><Layout /></ProtectedRoute>}>
              <Route index element={<Navigate to="/dashboard" replace />} />
              <Route path="dashboard" element={<Dashboard />} />
              
              {/* Adapter management */}
              <Route path="adapters" element={<AdapterList />} />
              <Route path="adapters/create" element={<AdapterConfiguration />} />
              <Route path="adapters/:id/edit" element={<AdapterConfiguration />} />
              
              {/* File management */}
              <Route path="files" element={<FileManagement />} />
              
              {/* SSH key management */}
              <Route path="ssh-keys" element={<SshKeyManagement />} />
              
              {/* User management (admin only) */}
              <Route path="users" element={<UserManagement />} />
              
              {/* Monitoring and logs */}
              <Route path="monitoring" element={<Monitoring />} />
              <Route path="logs" element={<Logs />} />
            </Route>
            
            {/* Catch all - redirect to dashboard */}
            <Route path="*" element={<Navigate to="/dashboard" replace />} />
          </Routes>
          
          {/* Global notifications */}
          <Toaster 
            position="top-right"
            toastOptions={{
              duration: 4000,
              style: {
                background: '#363636',
                color: '#fff',
              },
              success: {
                style: {
                  background: '#10b981',
                  color: '#fff',
                },
              },
              error: {
                style: {
                  background: '#ef4444',
                  color: '#fff',
                },
              },
            }}
          />
        
        {/* React Query DevTools - only in development */}
        {import.meta.env.DEV && <ReactQueryDevtools initialIsOpen={false} />}
      </div>
  )
}

export default App